import React from 'react';
import { useFormContext } from 'react-hook-form';

export const Step1: React.FC = () => {
  const { register, watch } = useFormContext();
  const selectedService = watch('service');

  const services = [
    { id: 'therapy', name: '', description: ' ' },
    { id: 'surgery', name: '', description: ' ' },
    { id: 'pediatrics', name: '', description: ' ' },
    { id: 'dentistry', name: '', description: ' ' },
  ];

  return (
    <div>
      <h3 style={{ marginBottom: '16px' }}> </h3>
      <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
        {services.map(service => (
          <label
            key={service.id}
            style={{
              padding: '16px',
              border: `2px solid ${selectedService === service.id ? '#2d5bff' : '#e9ecef'}`,
              borderRadius: '8px',
              cursor: 'pointer',
              backgroundColor: selectedService === service.id ? '#f0f4ff' : 'white',
            }}
          >
            <input
              type="radio"
              value={service.id}
              {...register('service', { required: ' ' })}
              style={{ marginRight: '12px' }}
            />
            <div>
              <div style={{ fontWeight: 'bold' }}>{service.name}</div>
              <div style={{ fontSize: '14px', color: '#666' }}>{service.description}</div>
            </div>
          </label>
        ))}
      </div>
    </div>
  );
};
