import { commonApi } from '@/store/common.api.ts';
import { mockQueryFn } from '@/api/services/utils.ts';
import {
  IDoctor,
  ILpus,
  ISpecialty,
  ITimeTable,
} from '@/api/services/lpus-controller/lpus-controller.types.ts';
import {
  mockDoctorsData,
  mockLpusData,
  mockSpecialtiesData,
  mockTimetableData,
} from '@/api/services/lpus-controller/lpus-controller.data.ts';

export const bookingDictionaryController = commonApi.injectEndpoints({
  endpoints: builder => ({
    getLpus: builder.query<ILpus[], void>({
      // query: () => 'lpus',
      queryFn: () => {
        return mockQueryFn('lpu', mockLpusData);
      },
    }),
    getSpecialties: builder.query<ISpecialty[], void>({
      // query: () => 'specialties',
      queryFn: () => {
        return mockQueryFn('specialties', mockSpecialtiesData);
      },
    }),
    getDoctors: builder.query<IDoctor[], void>({
      // query: () => 'specialties',
      queryFn: () => {
        return mockQueryFn('specialties', mockDoctorsData);
      },
    }),
    getTimetable: builder.query<ITimeTable[], { doctorId: string }>({
      // query: () => 'specialties',
      queryFn: () => {
        return mockQueryFn('specialties', mockTimetableData);
      },
    }),
  }),
});

export const {
  useGetLpusQuery,
  useGetSpecialtiesQuery,
  useGetDoctorsQuery,
  useLazyGetTimetableQuery,
} = bookingDictionaryController;
